            <dv-border-box-3 class="rmctc-left-container">
              <Center-Cmp v-if="sonRefresh" :topData="topData"/>
            </dv-border-box-3>

            <!-- <div class="rmctc-right-container">
              <dv-border-box-3 class="rmctc-chart-1">

                <Right-Chart-1 />

              </dv-border-box-3>

              <dv-border-box-4 class="rmctc-chart-2" :reverse="true">

                <Right-Chart-2 />

              </dv-border-box-4>
            </div> -->
          </div>

          <dv-border-box-4 class="rmc-bottom-container">
            <Bottom-Charts v-if="sonRefresh" :bottomData="bottomData"/>
          </dv-border-box-4>
        </div>

        <dv-border-box-3 class="left-chart-container">
          <Left-Chart-1 v-if="sonRefresh" :leftData="leftData"/>
          <!-- <Left-Chart-2 /> -->
          <!-- <Left-Chart-3 /> -->
        </dv-border-box-3>
      </dv-border-box-1>
