<script>
import LeftChart1 from "./LeftChart1";
import LeftChart2 from "./LeftChart2";
import LeftChart3 from "./LeftChart3";

import CenterCmp from "./CenterCmp";

import RightChart1 from "./RightChart1";
import RightChart2 from "./RightChart2";

import BottomCharts from "./BottomCharts";

export default {
  name: "DataView",
  components: {
    LeftChart1,
    LeftChart2,
    LeftChart3,
    CenterCmp,
    RightChart1,
    RightChart2,
    BottomCharts,
  },
  data() {
    return {
      sonRefresh: false,
      leftData: [],
      topData: null,
      bottomData: []
    };
  },
  async mounted() {
    let test = window.location.href
    let port = window.location.port
   await this.getData();
   await this.refreshFlag()
   clearInterval(myTimeDisplay);
    let myTimeDisplay = setInterval(() => {
      this.getData(); //每分钟调用一次
    }, 60000);
  },
  methods: {
    async getData() {
      let date = this.formatDate(new Date(), 'yyyy-MM-dd')
      let { data: res } = await this.axios.get(
        `/screen/findScreenData?time=${date}`
      );
      let allData = res.data
      this.leftData = allData.flowInfoVOList
      this.topData = allData
      this.bottomData = allData.produceInfoVOList
      // localStorage.setItem('flowInfoVOList', allData.flowInfoVOList)
      // localStorage.setItem('orderInfoVO', allData.orderInfoVO)
      // localStorage.setItem('orderMonthVOList', allData.orderMonthVOList)
      // localStorage.setItem('produceInfoVOList', allData.produceInfoVOList)
    },
    refreshFlag(){
         this.sonRefresh = true
    },
    formatDate(date, fmt) {
      //获取年份
      if (/(y+)/.test(fmt)) {
        // 把数字变成字符串
        let dateY = date.getFullYear() + "";
        //RegExp.$1 在判断中出现过，且是括号括起来的，所以 RegExp.$1 就是 "yyyy"
        fmt = fmt.replace(RegExp.$1, dateY.substr(4 - RegExp.$1.length));
      }

      //获取其他
      let o = {
        "M+": date.getMonth() + 1,
        "d+": date.getDate(),
        "h+": date.getHours(),
        "m+": date.getMinutes(),
        "s+": date.getSeconds(),
      };
      for (const k in o) {
        if (new RegExp(`(${k})`).test(fmt)) {
          let str = o[k] + "";
          fmt = fmt.replace(
            RegExp.$1,
            RegExp.$1.length == 1 ? str : this.padLeftZero(str)
          );
        }
      }
      return fmt;
    },
    padLeftZero(str) {
      return ("00" + str).substr(str.length);
    },
  },
};
</script>

