<script>
import LabelTag from "./LabelTag";

export default {
  name: "CenterCmp",
  components: {
    LabelTag,
  },
  props: {
    topData: {
      type: Object,
      default: null,
    },
  },
  data() {
    return {
      config: {
        data: [
          {
            name: "收费站",
            value: 1315,
          },
          {
            name: "监控中心",
            value: 415,
          },
          {
            name: "道路外场",
            value: 90,
          },
          {
            name: "其他",
            value: 317,
          },
        ],
        color: ["#00baff", "#3de7c9", "#fff", "#ffc530", "#469f4b"],
        lineWidth: 30,
        radius: "55%",
        activeRadius: "60%",
      },

      labelConfig: {
        data: ["收费站", "监控中心", "道路外场", "其他"],
      },
      option: {
        legend: {
          textStyle: {
            fill: "#fff",
          },
        },
        xAxis: {
          // data: [
          //   "1月",
          //   "2月",
          //   "3月",
          //   "4月",
          //   "5月",
          //   "6月",
          //   "7月",
          //   "8月",
          //   "9月",
          //   "10月",
          //   "11月",
          //   "12月",
          // ],
          data: [],
          boundaryGap: false,
          axisLine: {
            style: {
              stroke: "#999",
            },
          },
          axisLabel: {
            style: {
              fill: "#999",
            },
          },
          axisTick: {
            show: false,
          },
        },
        yAxis: {
          data: "value",
          splitLine: {
            show: false,
          },
          axisLine: {
            style: {
              stroke: "#999",
            },
          },
          axisLabel: {
            style: {
              fill: "#999",
            },
            formatter({ value }) {
              return value.toFixed(2);
            },
          },
          axisTick: {
            show: false,
          },
          min: 0,
          max: 40000,
        },
        series: [
          {
            // data: [
            //   2220, 3440, 3570, 2260, 4670, 5500, 4400, 4800, 3900, 3700, 4600,
            //   3400,
            // ],
            data: [],
            type: "line",
            name: "设备完好率",
            smooth: true,
            lineArea: {
              show: true,
              gradient: ["rgba(55, 162, 218, 0.6)", "rgba(55, 162, 218, 0)"],
            },
            linePoint: {
              radius: 4,
              style: {
                fill: "#00db95",
              },
            },
          },
        ],
      },
      times: "",
      timer: null,
      isShow: false,
      //订单内盒
      orderNumIn: 0,
      //订单外箱
      orderNumOut: 0,
      //订单总数
      orderAll: 0,
      //工单内盒
      workNumIn: 0,
      //工单外箱
      workNumOut: 0,
      //工单总数
      workAll: 0,
      //头部总数展示数据
      arrNum: [],
    };
  },
  created() {
    // this.getTime()
    this.updateTime();
  },
  mounted(){
    this.updateData()
  },
  //销毁定时器
  beforeDestroy: function () {
    if (this.timer) {
      // console.log("销毁定时器");
      clearTimeout(this.timer); //在Vue实例销毁前，清除定时器
    }
  },
  methods: {
    updateData(){
      //首次渲染清空折线图横坐标纵坐标数据
      this.option.xAxis.data = []
      this.option.series[0].data = []
      let orderInfoVO = this.topData.orderInfoVO
      this.orderNumIn = orderInfoVO.orderNumIn
      this.orderNumOut = orderInfoVO.orderNumOut
      this.orderAll = this.orderNumIn + this.orderNumOut
      this.workNumIn = orderInfoVO.workNumIn
      this.workNumOut = orderInfoVO.workNumOut
      this.workAll = this.workNumIn + this.workNumOut
      //顶部订单总数数据处理
      //将订单总量数字转为字符串再转为数据，然后遍历展示
      this.arrNum = this.orderAll.toString().split('')
      //订单量月趋势处理
      let orderMon = this.topData.orderMonthVOList
      orderMon.forEach(item => {
         this.option.xAxis.data.push(item.month)
         this.option.series[0].data.push(item.orderToldNum)
        //  this.option.series.data.push(item.orderToldNum)
      });
      this.isShow = true
    },
    getTime() {
      let now = new Date();
      let days = function (val) {
        if (!val) return "";
        const date = new Date(val);
        const Y = date.getFullYear() + "-";
        const M =
          (date.getMonth() + 1 < 10
            ? "0" + (date.getMonth() + 1)
            : date.getMonth() + 1) + "-";
        const D =
          date.getDate() > 9
            ? date.getDate() + " "
            : "0" + date.getDate() + " ";
        const h =
          date.getHours() > 9
            ? date.getHours() + ":"
            : "0" + date.getHours() + ":";
        const m =
          date.getMinutes() > 9
            ? date.getMinutes() + ":"
            : "0" + date.getMinutes() + ":";
        const s =
          date.getSeconds() > 9 ? date.getSeconds() : "0" + date.getSeconds();
        return Y + M + D + h + m + s;
      };
      this.times = days(now);
    },

    //定时器函数
    updateTime() {
      this.getTime();
      this.timer = setTimeout(() => {
        this.updateTime();
      }, 1000);
    },
  },
};
</script>

